/* Tailwind Setup */
@tailwind base;
@tailwind components;
@tailwind utilities;

/* Styling Tabs */
.tabs-list {
  display: flex;
  gap: 16px;
  justify-content: center;
  margin-bottom: 1rem;
}

.tab-trigger {
  padding: 12px 20px;
  font-size: 16px;
  font-weight: 600;
  background-color: #edf2f7;
  color: #4a5568;
  border: 1px solid #e2e8f0;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
}

.tab-trigger[data-state='active'] {
  background-color: #2563eb;
  color: white;
  border: 1px solid #1d4ed8;
}

.tab-trigger:hover {
  background-color: #cbd5e0;
}

/* Dropdown Menu Styling */
.dropdown-container {
  display: flex;
  justify-content: center;
  margin-bottom: 16px;
}

.select-trigger {
  width: 220px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 12px;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  background-color: white;
  font-size: 16px;
  cursor: pointer;
}

.select-trigger svg {
  width: 16px;
  height: 16px;
}

.select-content {
  position: absolute;
  margin-top: 8px;
  width: 220px;
  background-color: white;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  z-index: 10;
}

.select-item {
  padding: 10px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.select-item:hover {
  background-color: #f3f4f6;
}

/* Chart Styling */
.chart-title {
  font-size: 1.5rem;
  font-weight: 700;
  text-align: center;
  margin-bottom: 12px;
}

.bar-chart-wrapper {
  flex: 1;
  min-width: 0;
}

.labels-container {
  width: 120px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.label-item {
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  white-space: nowrap;
}

/* Responsive Layout */
.dashboard-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 16px;
}
